.pen-size-wrapper {
  width: 220px;
  height: 120px;
  margin: 5px auto;
  background-color: #333333;
  color: #aaa;
  display: flex;
  flex-direction: column;

}

.pen-size-wrapper p {
  margin: 5px;
}

.pen-sizes {
  list-style: none;
  margin-left: 25px;
}

.pen-sizes li {
  margin-bottom: 3px;
  display: flex;
  align-items: center;
}

.pen-sizes li  input{
  margin-left: 5px;
}



